* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app-wrapper {
  background: rgb(255, 230, 0);
  width: 100vw;
}

.input-wrapper {
  display: flex;
  justify-content: center;
}

.app-input {
  width: 600px;
  height: 30px;
  margin-bottom: 20px;
  margin-top: 20px;
}

.pokemon-list-wrapper {
  width: 900px;
  margin: 0 auto;
}

.pokemon-list {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px;
  list-style: none;
}


.card-wrapper {
  background: rgb(221, 221, 221);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-family: sans-serif;
}

.card-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.card-id {
  background: rgb(187, 187, 187);
  color: gray;
  padding: 2px 6px;
  width: 50px;
  border-radius: 2px;
  font-size: 15px;
}

.wrapper-secondary {
  background: white;
  flex-grow: 1;
  padding: 10px 10px;
}

.card-type {
  list-style: none;
  display: inline;
  margin-right: 5px;
  padding: 2px;
  border: 1px solid rgb(148, 146, 146);
  border-radius: 5px;
  color: rgb(121, 119, 119);
  font-size: 10px;
}
